$color-SmokeyGrey: hsl(0, 1%, 44%);
$color-White: hsl(0, 0%, 100%);
$color-OffWhite: hsl(0, 0%, 94%);
$color-LightGrey: hsl(0, 0%, 86%);
$color-OffBlack: hsl(0, 0%, 8%);
$color-Purple: hsl(259, 100%, 65%);
$color-Light-red: hsl(0, 100%, 67%);

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,700;1,400;1,800&display=swap');

.App{
    height: 100vh;
    background-color: $color-OffWhite;
    display: flex;
    justify-content: center;
    align-items: center;
    @font-face {
        font-family: 'Poppins', sans-serif;
    }
    font-family: 'Poppins', sans-serif;
    .main-container{
        background-color: $color-White;
        height: 80%;
        
        border-bottom-right-radius: 30%;
    }
    form{
       
        .part{
            margin-bottom: 20px;
        }
        label{
           color: $color-SmokeyGrey;
           font-size: 12px;
           text-transform: uppercase;
           letter-spacing: 5px;
           font-weight: bold;
           margin-bottom: 5px;
        }
        ::placeholder{
            color: $color-LightGrey;
            font-weight: bold;
            
        }
        button{
            background-color: $color-Purple;
            border: none;
            width: 50px;
            height: 50px;
            background-image: url(./img/icon-arrow.svg);
            background-repeat: no-repeat;
            background-position: center;
            &:hover{
                background-color: $color-OffBlack;
            }
        }
        input{
            font-size: 32px;
            width: 99%;
            font-weight: bold;
           
            border-radius: 5px;
           &:focus{
            outline: 1px solid $color-Purple;

           }
          
        }
        .validation{
            color: red;
            letter-spacing: 1px;
            
           }
        
      
    }
    h1{
        font-size: 80px;
        font-weight: 800;

        span{
            color: $color-Purple;
        }
    }

    @media screen and (max-width: 1280px) {
       
        h1{
            font-size: 70px;
            
        }
    
}
    @media screen and (max-width: 640px) {
        form{
            label{
                font-size: 16px;
                margin-right: 20px;
            }
            input{
                font-size: 24px;
              width: 60% !important;
            }
            hr{
                width: 100%;
            }
            ::placeholder{
                font-size: 18px;
            }
          
        }
        .main-container{
            width: 90% !important;
        }
        .part{
            flex-direction: column;
        }
        h1{
            font-size: 42px;
        }
        .btn-hr{
            margin-block: 20px;
        }
        .number{
            flex-direction: column;
        }
    }
 
}
